import * as React from "react";
import Typography from "@mui/material/Typography";
import Modal from "@mui/material/Modal";
import { MuiFileInput } from "mui-file-input";
import { StyledBoxforModal } from './ModalStyles';

const EditPhotoModal = ({ handleCloseModal, openModal, savePhoto }) => {
  const [file, setFile] = React.useState(null);

  const handleChange = (newFile) => {
    setFile(newFile);
    savePhoto(newFile);
    setTimeout(() => {
      handleCloseModal();
    }, 2000);
  };
  return (
    <div>
      <Modal open={openModal} onClose={handleCloseModal}>
        <StyledBoxforModal>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            행햟햡혞향햨햟 햫쮏쒫쮏 혟쮐쮏햟혟햦햦
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            뉌 햪쮏웷왐햣 향햟햡혞향햦혝혧 햦향쮏쐄햟햤햣햫햦햣  혟쮐햪햟혝햣 jpg 햦햩햦 png.
          </Typography>
          <MuiFileInput sx={{ mt: 2 }} value={file} onChange={handleChange} />
        </StyledBoxforModal>
      </Modal>
    </div>
  );
};

export default EditPhotoModal;
